body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #edf0f5;
}

.mui-block {
  display: block !important;
  margin-bottom: 16px !important;
}

.padding {
  padding: 16px;
  margin-bottom: 16px;
}

.mui-full-width {
  width: 100% !important;
  margin-bottom: 16px !important;
}

.progress-bar-placeholder {
  width: 100%;
  height: 4px;
}

.header {
  background: #025002 !important;
  text-align: center;
  padding: 8px;
}
.header__game-id {
  font-size: 75%;
}
.header h1 {
  margin: 0;
}
.header__military-translation {
  font-size: 75%;
}

.home {
  background: white;
  padding-bottom: 32px;
}

.home-content {
  width: 50%;
  max-width: 300px;
  margin: 0 auto;
  padding-top: 8px;
}

.home-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.home-secion--separator {
  text-align: center;
  margin-bottom: 16px;
  text-transform: uppercase;
  font-weight: bold;
}

.create-game-label {
  margin-top: 8px;
}

.game-waiting-room {
  text-align: center;
  display: flex;
  justify-content: center;
  min-height: 85vh;
  align-items: center;
  background: #fff;
}

.game-chat {
  width: 40%;
  max-width: 200px;
  min-width: 180px;
  background: #837065;
  display: flex;
  flex-direction: column;
  padding: 0;
}

.message-board {
  flex: 1;
  display: flex;
  flex-direction: column;
  max-height: 340px;
  overflow-y: auto;
  padding-top: 15px;
}

.message {
  font-size: 80%;
  margin-bottom: 6px;
  animation: moveUpIn 2s ease-out;
}

.message-text {
  padding: 2px 6px;
  border-radius: 10px;
}

.message-mine {
  text-align: right;
  margin-right: 6px;
}
.message-mine .message-text {
  background: #fff;
}

.message-their {
  text-align: left;
}
.message-their .message-text {
  background: #dedede;
}

@keyframes moveUpIn {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.codename {
  position: relative;
}
.codename--5 {
  width: 19%;
}
.codename__background {
  width: 100%;
  height: 100%;
}
.codename__word {
  position: absolute;
  top: 60%;
  left: 0;
  right: 0;
  text-transform: uppercase;
  width: 100%;
  text-align: center;
  font-size: 90%;
}
.codename__word-updsidedown {
  position: absolute;
  top: 35%;
  left: -10%;
  text-transform: uppercase;
  transform: rotate(180deg);
  opacity: 0.5;
  font-size: 60%;
  width: 100%;
  text-align: center;
  font-style: italic;
}

.loading-page {
  height: 98vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.codenames-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background: #837065;
}

.game-area {
  display: flex;
  width: 100%;
  background: orange;
}

.game-timeline {
  width: 40%;
  background: #837065;
  max-width: 200px;
  min-width: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
}
